import { Injectable } from '@angular/core';
import { AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SlideshowService {

  page: number;
  chapter: number;
  chapterTitle: string;
  isLastPage: boolean = false;
  imageUrl: Observable<string | null>;
  nextImageUrl: Observable<string | null>;

  constructor(private afStorage: AngularFireStorage) {
    this.page = 1;
    this.chapter = 1;

    this.setImageUrl();

    this.setNextImageUrl();

  }

  public nextPage() {
    this.page = this.page + 1;
    this.imageUrl= this.nextImageUrl;
    this.setNextImageUrl();

  }

  public previousPage() {
    this.page = this.page - 1;
    this.nextImageUrl= this.imageUrl;
    this.setImageUrl();
  }


  setImageUrl() {

    const ref = this.afStorage.ref(this.page + '.jpg');
    this.imageUrl = ref.getDownloadURL();
  }
  setNextImageUrl() {

    const ref2 = this.afStorage.ref((this.page + 1 )+ '.jpg');
    this.nextImageUrl = ref2.getDownloadURL();
  }

}
